.home_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 98%; /* 親コンテナの幅に合わせる */
}

/* コンテナ */
.home_body_container {
    border: 1px solid #000;
    padding: 10px;
    margin: 5px;
    width: 100%; /* フレキシブルな横幅を指定 */
    max-width: 600px; /* 最大幅を設定 */
    box-sizing: border-box; /* パディングを含めたボックスサイズを指定 */
  }

.rate_of_inc_container{
    width: 100%;
    margin-bottom: 10px;
}

/* レッスンセレクタ */
.selector_container {
    display: flex;
}